import React, { Component } from "react";
import Link from "next/link";

const linkStyle = {
  marginRight: 15
};

const layoutStyle = {
  margin: 20,
  padding: 20,
  border: "1px solid #ddd"
};

class Header extends Component {
  render() {
    return (
      <div>
        <Link href="/">
          <a style={linkStyle}>Home</a>
        </Link>
        <Link href="/about">
          <a style={linkStyle}>About</a>
        </Link>
      </div>
    );
  }
}

export default Header;
